import React from 'react';
import Sidebar from './SidebarComponents/Sidebar';
import PopularPost from './SidebarComponents/PopularPost';
//умная компанента. умеет отрисовывать форму логина и популярные посты
const SidebarContent = (props) => {
    return (
    	<div id="sidebar">
			<Sidebar userName={props.userName} 
					 userPass={props.userPass}
					 userStay={props.userStay}
					 checkUser={props.checkUser}
					 user={props.user}
			/>
			<PopularPost />
		</div>
  )
}

export default SidebarContent;